import React, {Component} from 'react';
import './Home.css';
import Draggable from 'react-draggable';
import {Link} from 'react-router-dom';

	 class Home extends Component {
		 state = {
		 };

		 render() {
				return (
                    <React.Fragment>
                        <div className="mainPhoto">
                            <Draggable>
                                <div className="tool Blog"> <Link to="/blog"> <div className="fakeNav"> </div> </Link> </div>
                            </Draggable>
                            <Draggable>
                                <div className="tool Teaching"> <Link to="/teaching"> <div className="fakeNav"> </div> </Link> </div>
                             </Draggable>
                            <Draggable>
                                <div className="tool Awards"> <Link to="/awards"> <div className="fakeNav"> </div> </Link> </div>
                             </Draggable>
                             <Draggable>
                                <div className="tool Contact"> <Link to="/contact"> <div className="fakeNav"> </div> </Link> </div>
                             </Draggable>
                             <Draggable>
                                <div className="tool Picture"> <Link to="/about"> <div className="fakeNav"> </div> </Link> </div>
                             </Draggable>
                             <Draggable>
                                <div className="tool Projects"> <Link to="/projects"> <div className="fakeNav"> </div> </Link> </div>
                             </Draggable>

                         </div>
                    </React.Fragment>
				);


		 }
	 }

	export default Home;
